.container{
@media only screen and (min-width: 768px) {
 display: flex;
 justify-content: flex-end;
 padding-top: 31px;
}
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;

  @media only screen and (min-width: 768px) {
  background-color: rgba(0, 0, 0, 0.5);
  
  }
}

.btn-open{
display: block;
margin-left: auto;
margin-right: auto;
margin-top: 52px;
width: 128px;
height: 40px;
border-radius: 8px;
border: 1px solid #FFF;
background-color: transparent;

color: #FFF;

text-align: center;
font-family: Barlow Semi Condensed;
font-size: 16px;
font-weight: 600;
letter-spacing: 2.5px;
text-transform: uppercase;

@media only screen and (min-width: 768px) {
  margin: 0;
}
}

.modal {
  height: 100vh;
  border-radius: 8px;
  background: #fff;

  box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.2);

 
  
  
  @media only screen and (min-width: 768px) {

width: 400px;
height: 415px;
  }
}



.modal-container{
    padding: 70px 35.7px 66px 35.7px;
    
    @media only screen and (min-width: 768px) {
      padding: 32px 32px 32.5px 49px;
      position: relative;
    }
}

.title {
    margin-top: 0;
    margin-bottom: 80px;
    text-align: center;
  color: #3b4262;
  font-size: 32px;
  font-weight: 700;
  line-height: 32px; /* 100% */
  text-transform: uppercase;


  @media only screen and (min-width: 768px) {
    text-align: left;
    margin-bottom: 48px;
  }
}

